import React from 'react';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { AiOutlineCopy } from 'react-icons/ai';
import { useNotistack } from '../../../shared/hooks';
import IconButton from '@material-ui/core/IconButton';
import { useTranslation } from 'react-i18next';

type Props = {
	copiedEmail: any;
	isDisabled?: boolean;
};

export default function CopyEmailButton({ copiedEmail, isDisabled = true }: Props) {
	const { showNotification } = useNotistack();
	const { t } = useTranslation();
	return (
		<CopyToClipboard
			text={copiedEmail}
			onCopy={() => {
				showNotification({
					message: t('email_copied'),
					variant: 'info'
				});
			}}
		>
			<IconButton
				className="border-0 !px-0 !py-0 text-kgrey hover:text-primary disabled:opacity-60 disabled:hover:text-kgrey"
				disabled={!isDisabled}
			>
				<AiOutlineCopy className="cursor-pointer text-3xl transition transition" />
			</IconButton>
		</CopyToClipboard>
	);
}
