import React, { createContext, useReducer, useContext, useCallback, useMemo } from 'react';

type ContextType = {
	searchQuery: string;
	setSearchQuery: (query: string) => void;
	isStaff: string;
	setIsStaff: (type: string) => void;
};

// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
export const DepartmentContext = createContext<ContextType>(null!);
type setSearchQuery = {
	type: 'SET_SEARCH_QUERY';
	payload: string;
};

type setIsStaff = {
	type: 'SET_IS_STAFF';
	payload: string;
};
type StateType = {
	searchQuery: string;
	isStaff: string;
};

type Actions = setSearchQuery | setIsStaff;

const reducer = (state: StateType, action: Actions): StateType => {
	switch (action.type) {
		case 'SET_SEARCH_QUERY': {
			return {
				...state,
				searchQuery: action.payload
			};
		}
		case 'SET_IS_STAFF': {
			return {
				...state,
				isStaff: action.payload
			};
		}
		default:
			return state;
	}
};

export function DepartmentContextProvider({
	children
}: React.PropsWithChildren<Record<string, unknown>>) {
	const [state, dispatch] = useReducer(reducer, {
		searchQuery: '',
		isStaff: 'true'
	});

	const setSearchQuery = useCallback((query: string) => {
		dispatch({ type: 'SET_SEARCH_QUERY', payload: query });
	}, []);

	const setIsStaff = useCallback((type: string) => {
		dispatch({ type: 'SET_IS_STAFF', payload: type });
	}, []);

	// eslint-disable-next-line react/jsx-no-constructed-context-values
	const contextValues = useMemo(
		() =>
			({
				searchQuery: state.searchQuery,
				isStaff: state.isStaff,
				setSearchQuery,
				setIsStaff
			} as ContextType),
		[setSearchQuery, setIsStaff, state.searchQuery, state.isStaff]
	);

	return <DepartmentContext.Provider value={contextValues}>{children}</DepartmentContext.Provider>;
}

export const useDepartmentContext = () => {
	const departmentContext = useContext(DepartmentContext);

	if (!departmentContext)
		throw new Error('useBusinessContext must be used within the BusinessContextProvider');
	return departmentContext;
};
