import React, { useEffect, useMemo, useState } from 'react';
import { Field } from 'formik';
import { KntInputField, KntPhoneInputField, SelectField } from 'shared/components/Fields';
import { FileUploader, KntButton, Loader } from 'shared/components';
import { FormControlLabel, Radio, RadioGroup, WhiteModal } from 'shared/components/Elements';
import { IconButton, Tooltip } from '@material-ui/core';
import { AiFillInfoCircle } from 'react-icons/ai';
import { EntytyFormInstanceProps } from 'shared/typings/formik.types';
import { KntTwilioStyle } from 'shared/services';
import { isMobile } from 'react-device-detect';
import { useAppState } from 'shared/state';
import get from 'lodash/get';
import { AiOutlineCloseCircle } from 'react-icons/ai';
import config from 'config';
import ChooseIcon from './ChooseIcon';
import { useDepartmentContext } from '../context/DepartmentContext';
import { IOSSwitch } from '../../../shared/components/Elements/Fields/SwitchComp';
import { useSwitch } from '../../../shared/hooks';
import AsyncSelectJs from '../../../shared/components/Fields/AsyncSelectJs';
import CaseChatDataFields from './CaseChatDataFields';
import CopyEmailButton from './CopyEmailButton';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';

const imgPath = `${config.PUBLIC_ASSETS}img`;

type Props = {
	values: any;
	isCreate?: boolean;
	department?: any | null;
	closeModal: () => void;
	id?: number;
	departmentId?: number;
	formValuesSetter?: (fieldValues: string[]) => void;
	setAccStatusRequired: any;
	open?: {
		acc_status?: Record<string, number>;
		acc_linked_email: string | null;
	};
} & EntytyFormInstanceProps;

export default function DepartmentFormElements({
	values,
	setFieldValue,
	setFieldError,
	isSubmitting,
	errors,
	closeModal,
	resetForm,
	isCreate,
	id,
	departmentId,
	formValuesSetter = () => null,
	setAccStatusRequired,
	open
}: Props) {
	const { t } = useTranslation();
	const { companyId } = useAppState();

	console.log(open, 'open');

	const { checked, handleChange } = useSwitch({
		assignRequired: isCreate ? false : values?.assign_required
	});
	const { checked: checkedAutoCase, handleChange: handleChangeVoiceMail } = useSwitch({
		autoCaseCreate: isCreate ? false : values?.auto_case_create
	});

	const [assigneeFunction, setAssigneeFunction] = useState(values?.link ? 'link' : 'caseChat');
	const [chooseIcon, setChooseIcon] = useState<boolean>(false);
	const [caseChatModal, setCaseChatModal] = useState<boolean>(false);
	const { isRTL } = useTranslationHelpers();

	const { isStaff, setIsStaff } = useDepartmentContext();
	const [type, setType] = useState(isStaff);

	const handleModalOpening = () => setChooseIcon((prev) => !prev);
	const handleCaseChatModal = () => setCaseChatModal((prev) => !prev);
	const closeModalChooseIcon = () => setChooseIcon(false);
	const closeModalCaseChat = () => setCaseChatModal(false);

	useEffect(() => {
		if (isCreate) {
			setIsStaff(type);
			setFieldValue('is_staff', type);
		}
	}, [type]);

	const [uploadMeta, setUploadMeta] = useState<any>(null);
	const [cancelSending, setCancelSending] = useState<any>(false);
	const [icon, setIcon] = useState(isCreate ? values?.icon : null);

	useEffect(() => {
		if (icon) setFieldValue('icon', icon);
	}, [icon]);

	useEffect(() => {
		if (assigneeFunction === 'link') {
			setFieldError('link', 'Required');
		}
	}, [assigneeFunction]);

	useEffect(() => {
		if (id) {
			setFieldValue('department', { id: id });
		}
	}, []);

	useEffect(() => {
		if (isCreate) {
			const checkableFields = [
				'name',
				'link',
				'phone',
				'case_escalation_time',
				'initial_response_time'
			];

			const fieldValues = Object.keys(values).filter((field) => {
				const fieldHasValue = Array.isArray(values[field]) ? values[field].length : values[field];
				if (fieldHasValue && checkableFields.includes(field)) {
					return values[field];
				}
			});
			formValuesSetter(fieldValues);
		}
	}, [values]);

	useEffect(() => {
		setFieldValue('assign_required', checked.assignRequired);
	}, [checked.assignRequired]);

	useEffect(() => {
		setFieldValue('auto_case_create', checkedAutoCase.autoCaseCreate);
		setAccStatusRequired(checkedAutoCase.autoCaseCreate);

		if (!checkedAutoCase.autoCaseCreate) {
			setFieldValue('acc_linked_email', null);
			setFieldValue('acc_status', undefined);
		} else if (open) {
			setFieldValue('acc_linked_email', open?.acc_linked_email);
			setFieldValue('acc_status', open?.acc_status);
		}
	}, [checkedAutoCase.autoCaseCreate, values.auto_case_create]);

	const handleCaseTypeChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
		setType(evt.target.value);
	};
	const handleAssigneeFunctionChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
		const value = evt.target.value;
		setAssigneeFunction(value);

		if (value === 'caseChat') {
			setFieldValue('link', null);
		}
	};
	const handleOpenChooseIconModal = () => {
		setChooseIcon(true);
	};

	const timeOptions = useMemo(
		() => [
			{ label: t('minutes'), value: 'MINUTES' },
			{ label: t('hours'), value: 'HOURS' },
			{ label: t('days'), value: 'DAYS' }
		],
		[t]
	);

	return (
		<div id={'department-form'}>
			<div className="branch-form__header flex-center-between -mt-5 border-b border-kgrey-dark-1/30 px-6 py-4">
				<div className="branch-form__heading font-base font-medium">
					{isCreate ? t('create_new_department') : t('edit_department')}
				</div>
				{isCreate ? (
					<div
						className="branch-form__reset cursor-pointer text-sm text-kgrey transition hover:text-kdark"
						onClick={resetForm}
					>
						{t('clear')}
					</div>
				) : null}
			</div>
			<div className=" w-100 p-6">
				<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">{t('image')}</div>
				<div className="mb-5 flex items-center">
					<div className="user-avatar-box__display flex-centered  relative h-20 w-20 overflow-hidden rounded-full dark:bg-kdark">
						<img
							src={
								isCreate
									? get(icon, 'thumbnail') || `${imgPath}/chat-placeholder.png`
									: get(values?.icon, 'thumbnail') || `${imgPath}/chat-placeholder.png`
							}
							alt=""
							className={'object-fit'}
						/>
						{uploadMeta !== null && (
							<div
								className={
									'absolute top-1/2 left-1/2 z-10 h-10 w-10 -translate-y-1/2 -translate-x-1/2'
								}
							>
								<div className="upload-media-spinner">
									<Loader variant="determinate" value={uploadMeta} />
								</div>
								<div
									onClick={() => {
										setCancelSending(true);
										setUploadMeta(null);
									}}
									className={
										'absolute top-1/2 left-1/2 z-20 h-8 w-8 -translate-y-1/2 -translate-x-1/2 ' +
										'cursor-pointer text-3xl text-primary-dark transition hover:text-primary'
									}
								>
									<AiOutlineCloseCircle />
								</div>
							</div>
						)}
					</div>
					<div className="ml-5">
						<FileUploader
							uploadMeta={uploadMeta}
							setUploadMeta={setUploadMeta}
							cancelSending={cancelSending}
							setCancelSending={setCancelSending}
							setFile={setIcon}
						/>
					</div>
				</div>
				<div
					onClick={handleOpenChooseIconModal}
					className="darl:text-white mb-4 inline-block cursor-pointer rounded-tl-10 rounded-tr-10 rounded-br-10 border border-dashed bg-kgrey-bg p-3.5 dark:bg-primary-300"
				>
					{t('choose_from_icon_library')}
				</div>

				<Field
					label={t('title')}
					name="name"
					component={KntInputField}
					placeholder={t('enter_title')}
				/>

				<div>
					<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">{t('function')}</div>
					<RadioGroup
						value={assigneeFunction}
						onChange={handleAssigneeFunctionChange}
						aria-label="Function"
						className="font-base !flex-row text-kgrey"
					>
						<FormControlLabel
							value="caseChat"
							control={<Radio color="primary" />}
							label={t('case_chat')}
						/>
						<FormControlLabel
							value="link"
							control={<Radio color="primary" />}
							label={t('hyperlink')}
						/>
					</RadioGroup>
					{assigneeFunction === 'link' ? (
						<Field
							label={t('hyperlink')}
							name="link"
							component={KntInputField}
							placeholder={t('hyperlink')}
						/>
					) : null}
				</div>

				{isCreate ? (
					<div>
						<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">{t('type')}</div>
						<RadioGroup
							value={type}
							onChange={handleCaseTypeChange}
							aria-label="Type"
							name="is_staff"
							className="font-base !flex-row text-kgrey"
						>
							<FormControlLabel
								value={'true'}
								control={<Radio color="primary" />}
								label={t('staff')}
							/>
							<FormControlLabel
								value={'false'}
								control={<Radio color="primary" />}
								label={t('client')}
							/>
						</RadioGroup>
					</div>
				) : null}

				<Field
					label={t('phone_number')}
					name="phone"
					component={KntPhoneInputField}
					placeholder={t('enter_phone')}
					enableSearch
					onChangeHandler={setFieldValue}
				/>
				<div className="mb-3.5">
					<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">
						{t('initial_required_response_time')}
					</div>
					<div className="mb-3.5 flex items-center">
						<Tooltip title={t('response_time_tooltip')} placement="top">
							<IconButton className={'mr-20 p-0'}>
								<AiFillInfoCircle />
							</IconButton>
						</Tooltip>
						<Field
							name="case_escalation_time"
							component={KntInputField}
							type="number"
							placeholder={t('time_lower')}
							classNames="!mb-0"
							labelClasses="hidden"
						/>
						<div className={clsx('flex-grow', isRTL ? 'mr-4' : 'ml-4')}>
							<Field
								className="flex-1"
								component={SelectField}
								styles={KntTwilioStyle}
								name="case_escalation_time_unit"
								defaultValue={timeOptions[0]}
								options={timeOptions}
								isSearchable={!isMobile}
							/>
						</div>
					</div>
					<div className="mb-3.5">
						<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">
							{t('case_escalation_time')}
						</div>
						<div className="mb-3.5 flex items-center">
							<Tooltip
								title="By defining this time, the system will alert the admins of this department when managers do not respond to a staff escalation within the timeframe designated for a specific case, when the case is initially created"
								placement="top"
							>
								<IconButton className={'mr-20 p-0'}>
									<AiFillInfoCircle />
								</IconButton>
							</Tooltip>
							<Field
								name="initial_response_time"
								component={KntInputField}
								type="number"
								placeholder={t('time_lower')}
								classNames="!mb-0"
								labelClasses="hidden"
							/>

							<div className={clsx('flex-grow', isRTL ? 'mr-4' : 'ml-4')}>
								<Field
									component={SelectField}
									styles={KntTwilioStyle}
									name="initial_response_time_unit"
									defaultValue={timeOptions[0]}
									options={timeOptions}
									isSearchable={!isMobile}
									className="flex-1"
								/>
							</div>
						</div>
					</div>
				</div>

				<div className="ml-1">
					<FormControlLabel
						control={
							<IOSSwitch
								checked={checked.assignRequired}
								onChange={handleChange}
								name="assignRequired"
							/>
						}
						label={t('assign_required')}
						className="mb-4"
						disabled={isCreate}
						classes={{ label: '!text-xs font-light text-kgrey-welcome' }}
					/>
				</div>
				{checked.assignRequired ? (
					<div className="react-select-dropdown mb-3.5">
						<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">
							{t('default_assignees')}
						</div>
						<Field
							component={AsyncSelectJs}
							customStylesProps={KntTwilioStyle}
							name="default_assignees"
							placeholder={t('select_default_assignees')}
							optionLabel={(option: any) =>
								`${get(option, 'user.first_name', '')} ${get(option, 'user.last_name', '')}`
							}
							loadOptionsUrl={`/${companyId}/departments/${departmentId}/users/`}
							loadOptionsParams={(search: string) => ({
								extra: {
									search
								}
							})}
							isSearchable
							maxMenuHeight={220}
							defaultValue={values?.default_assignees}
							isClearable
							isMulti
							loadOptionsKey="results"
							isCached
							onChange={(option: any) => {}}
						/>
					</div>
				) : null}
				<div className="ml-1 mb-0 flex items-center">
					<FormControlLabel
						control={
							<IOSSwitch
								checked={checkedAutoCase.autoCaseCreate}
								onChange={handleChangeVoiceMail}
								name="autoCaseCreate"
							/>
						}
						label={t('auto_case_creation')}
						classes={{ label: '!text-xs font-light text-kgrey-welcome' }}
					/>
					<div className="px-2">
						{values.auto_case_create ? (
							<CopyEmailButton
								isDisabled={!!values.acc_email_alias}
								copiedEmail={values.acc_email_alias}
							/>
						) : null}
					</div>
				</div>
				{checkedAutoCase.autoCaseCreate && (
					<>
						<div className="mb-2 ml-2 flex items-center text-sm font-light text-kgrey-menuText">
							{t('auto_case_creation_status')}
						</div>
						<div className="react-select-dropdown mb-3.5">
							<Field
								component={AsyncSelectJs}
								customStylesProps={KntTwilioStyle}
								name="acc_status"
								optionLabel="title"
								optionValue="id"
								isSearchable
								isCached
								placeholder={t('select_status')}
								reverse={true}
								loadOptionsUrl={`/${companyId}/my/company/case_statuses/`}
								// onChange={(option: any) => {
								// 	setState((prev: any) => ({ ...prev, status: option }));
								// }}
								loadOptionsParams={(search: string) => ({
									extra: {
										search,
										// is_staff: caseType === config.STAFF ? 'true' : ''
										is_staff: 'true'
									}
								})}
								loadOptionsKey=""
							/>
						</div>
						<div>
							<div className="flex-center-y mb-2 ml-2 text-sm font-light text-kgrey-menuText">
								<span className={'mr-1'}>{t('group_email_address')}</span>
								<Tooltip title={t('group_email_address_tooltip')} placement="top">
									<IconButton className={'mr-20 p-0'}>
										<AiFillInfoCircle />
									</IconButton>
								</Tooltip>
							</div>
							<div className="flex items-center">
								<Field
									type="email"
									name="acc_linked_email"
									component={KntInputField}
									placeholder={t('enter_email')}
									classNames={'!mb-0 flex-grow'}
									labelClasses={'hidden'}
								/>
							</div>
						</div>
					</>
				)}
			</div>
			<div className="branch-form__footer flex justify-between px-6 pt-4 pb-9">
				{!isCreate ? (
					<KntButton
						className={'dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'}
						type="button"
						onClick={handleCaseChatModal}
						color="primary"
					>
						{t('case_chat_data_field')}
					</KntButton>
				) : null}
				<div>
					<KntButton
						className={'mr-5 border border-kgrey text-kgrey'}
						color="default"
						onClick={closeModal}
					>
						{t('cancel')}
					</KntButton>
					<KntButton
						className={'dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'}
						type="submit"
						isSubmitting={isSubmitting}
						color="primary"
					>
						{isCreate ? t('create') : t('update')}
					</KntButton>
				</div>
			</div>
			<WhiteModal
				handleModalOpen={handleModalOpening}
				open={Boolean(chooseIcon)}
				scrollable
				noPadding
			>
				<ChooseIcon
					setIcon={setIcon}
					setFieldValue={setFieldValue}
					setChooseIcon={setChooseIcon}
					closeModal={closeModalChooseIcon}
				/>
			</WhiteModal>
			{!isCreate ? (
				<WhiteModal
					handleModalOpen={handleCaseChatModal}
					open={Boolean(caseChatModal)}
					scrollable
					noPadding
				>
					<CaseChatDataFields
						dataFields={values?.data_field}
						departmentId={departmentId}
						departmentName={values?.name}
						closeModalCaseChat={closeModalCaseChat}
						isSubDepartment={false}
						setFieldValue={setFieldValue}
					/>
				</WhiteModal>
			) : null}
			{isSubmitting ? (
				<div className="absolute inset-0 flex items-center justify-center bg-white bg-opacity-70">
					<Loader />
				</div>
			) : null}
		</div>
	);
}

DepartmentFormElements.defaultProps = {
	isCreate: true,
	department: null,
	// eslint-disable-next-line @typescript-eslint/no-empty-function
	resetForm: () => {}
};
