import React, { useState } from 'react';
import { useAppState } from 'shared/state';
import EntityContainer from '../../../modules/entity/containers';
import { Pagination } from '../../../shared/components/Elements';
import { Loader } from 'shared/components';
import { useAxios, usePagination } from '../../../shared/hooks';
import { CgSearch } from 'react-icons/cg';
import { IconButton } from '@material-ui/core';
import { AiOutlineClose } from 'react-icons/ai';
import { useDebounce } from 'use-debounce';
import { useTranslation } from 'react-i18next';

export default function ChooseIcon({ setFieldValue, setChooseIcon, closeModal, setIcon }: any) {
	const { t } = useTranslation();
	const { companyId } = useAppState();
	const [iconLoader, setIconLoader] = useState<boolean>(false);
	const { page, handlePage } = usePagination('iconPage');
	const [querySearch, setQuerySearch] = useState('');
	const [searchQuery] = useDebounce(querySearch, 500);

	const { fetchData: getSelectIcon } = useAxios({
		url: ``,
		dynamicUrl: true,
		method: 'put',
		cb: {
			success: async (data, args) => {
				setFieldValue('icon', data);
				setIconLoader(false);
				setIcon(data);
				setChooseIcon(false);
			},
			error: (error, args) => {
				console.log(error);
			}
		}
	});
	const setIconDepartment = (id: number) => {
		setIconLoader(true);
		getSelectIcon({ url: `${companyId}/icons/${id}/documentize/` });
	};

	return (
		<div className="w-[500px] px-3">
			<div className="flex items-center justify-between">
				<h4>{t('choose_from_library')}</h4>
				<IconButton onClick={closeModal}>
					<AiOutlineClose color="#979797" size="18px" />
				</IconButton>
			</div>
			<div className="group-sidebar__search relative mx-auto mb-5 w-[460px]">
				<input
					type="search"
					value={querySearch}
					className={
						'group-sidebar__search-input w-full border-b outline-none ' +
						'border-kgrey-border-solid' +
						' pl-7 transition focus:border-primary' +
						' placeholder-[rgba(45, 45, 45, 0.3)] pt-2 pb-2 dark:bg-kdark'
					}
					placeholder={t('search')}
					onChange={(e) => setQuerySearch(e.target.value)}
				/>
				<div className="absolute top-1/2 left-1 -translate-y-1/2">
					<div className="text-kgrey">
						<CgSearch />
					</div>
				</div>
			</div>
			{companyId ? (
				<EntityContainer.All
					entity={'iconsDepartment'}
					name={`iconsDepartment-${companyId}`}
					url={`/${companyId}/icons/`}
					params={{
						limit: 12,
						extra: {
							search: searchQuery
						},
						page
					}}
					replaceIds
				>
					{({ items, isFetched, meta }) => {
						return (
							<>
								<div>
									<div className="mb-6 flex min-h-[288px]  flex-wrap justify-around">
										{items.map((iconItem: any) => {
											const { id, icon, name } = iconItem;
											return (
												<div
													onClick={() => setIconDepartment(id)}
													className="mx-2 mb-4 flex w-24 cursor-pointer flex-col items-center rounded-md px-2 py-2 hover:shadow-md"
													key={id}
												>
													<img className="mb-3 inline-block h-8 w-8" src={icon} alt={name} />
													<p className="text-10px text-kgrey dark:text-white">{name}</p>
												</div>
											);
										})}
									</div>

									<div className="news__pagination my-4">
										{items.length ? (
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												defaultPage={6}
												siblingCount={1}
												color={'primary'}
												onChange={handlePage}
											/>
										) : null}
									</div>
								</div>

								{(!isFetched || iconLoader) && (
									<div className="mb-4 flex justify-center">
										<Loader tiled withOverlay />
									</div>
								)}
							</>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="mb-4 flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}
