import React, { useEffect, useState } from 'react';
import { KntButton, Loader } from '../../../shared/components';
import { FormControlLabel } from '../../../shared/components/Elements';
import { IOSSwitch } from '../../../shared/components/Elements/Fields/SwitchComp';
import IconButton from '@material-ui/core/IconButton';
import { AiFillDelete } from 'react-icons/ai';
import { nanoid } from 'nanoid';
import { useAxios, useNotistack, useUpdateEntities } from '../../../shared/hooks';
import { useAppState } from '../../../shared/state';
import { useDepartmentContext } from '../context/DepartmentContext';
import { useTranslation } from 'react-i18next';

export default function CaseChatDataFields({
	closeModalCaseChat,
	dataFields,
	departmentId,
	departmentName,
	isSubDepartment,
	setFieldValue
}: any) {
	const { t } = useTranslation();
	const [fields, setFields] = useState<any[]>(dataFields ? dataFields : []);
	const { showNotification } = useNotistack();
	const [loading, setLoading] = useState<boolean>(false);
	const { companyId } = useAppState();
	const { isStaff } = useDepartmentContext();
	const { updateEntities } = useUpdateEntities();

	console.log('dataFields', dataFields);

	const handleAddDataField = () => {
		const newField = {
			dataName: '',
			isRequired: false,
			id: nanoid()
		};
		setFields((prev: any) => [...prev, newField]);
	};

	useEffect(() => {
		if (!fields.length) {
			setFields([
				{
					dataName: '',
					isRequired: false,
					id: nanoid()
				}
			]);
		}
	}, []);

	const handleChange = (event: any, index: any) => {
		const target = event.target;
		const originTextValue = target.value;
		const value = target.type === 'checkbox' ? target.checked : originTextValue;
		const name = target.name;

		const oldFields = [...fields];
		const changedFields = oldFields.map((field, idx) =>
			idx === index ? { ...field, [name]: value } : field
		);

		setFields(changedFields);
	};

	const handleDelete = (field: any) => {
		const oldFields = [...fields];
		const changedFields = oldFields.filter((f) => f.id !== field.id && oldFields.length > 1);
		const filteredFields = changedFields.filter((field: any) => field.dataName !== '');
		setFields(filteredFields);
	};

	const { fetchData: handleSubmit } = useAxios({
		url: `/${companyId}/departments/${departmentId}/`,
		values: {
			data_field:
				fields?.length &&
				fields
					?.map((field: any) => ({
						...field,
						fieldName: field.dataName.toLowerCase().split(' ').join('_')
					}))
					.filter((field: any) => field.dataName !== ''),
			is_staff: isStaff === 'true',
			name: departmentName
		},
		method: 'patch',
		cb: {
			success: (data, args) => {
				setLoading(false);
				setFieldValue('data_field', data?.data_field);
				updateEntities({
					entity: isSubDepartment ? 'subDepartments' : 'departments',
					entityId: departmentId,
					updatingData: {
						data_field: data?.data_field
					}
				});
				closeModalCaseChat();
			},
			error: (error, args) => {
				setLoading(false);
				showNotification({
					message: error.response?.data?.message,
					variant: 'error'
				});
			}
		}
	});
	return (
		<div className="w-[500px] px-5">
			{loading ? (
				<div className="mb-4 flex justify-center">
					<Loader />
				</div>
			) : (
				<div>
					<h4 className="mb-5 text-xl">{t('department_data_fields')}</h4>
					{fields?.length
						? fields?.map((field: any, index: number) => {
								return (
									<div key={field.id} className="mb-4 flex items-center">
										<div className="mr-5 flex-grow ">
											<input
												name="dataName"
												value={field.dataName}
												placeholder={t('enter_department_question_name')}
												onChange={(evt) => handleChange(evt, index)}
												className="w-full rounded-lg border border-transparent bg-kgrey-light-grey-1 px-6 py-3.5 text-xs placeholder-current placeholder-kgrey outline-none transition hover:border-primary focus:border-primary focus:shadow-lg dark:bg-kdark-lighter dark:text-white"
											/>
										</div>
										<FormControlLabel
											control={
												<IOSSwitch
													checked={field.isRequired}
													onChange={(evt) => handleChange(evt, index)}
												/>
											}
											label={t('required')}
											name="isRequired"
											classes={{ label: '!text-xs font-light text-kgrey-welcome' }}
										/>
										<IconButton onClick={() => handleDelete(field)}>
											<AiFillDelete color="#979797" size="18px" />
										</IconButton>
									</div>
								);
						  })
						: null}
					<div className="flex justify-end">
						<KntButton
							className={'dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'}
							color="primary"
							onClick={handleAddDataField}
						>
							{t('additional_data_fields')}
						</KntButton>
					</div>
					<div className="branch-form__footer flex justify-end px-6 pt-4 pb-9">
						<KntButton
							className={'mr-5 border border-kgrey text-kgrey'}
							color="default"
							onClick={closeModalCaseChat}
						>
							{t('cancel')}
						</KntButton>
						<KntButton
							className={'dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'}
							type="submit"
							onClick={handleSubmit}
							color="primary"
						>
							{t('save')}
						</KntButton>
					</div>
				</div>
			)}
		</div>
	);
}
